<template>
  <div class="header">
    <div class="header__logo">
      <div class="header__login__bottom_1" ></div>
      <img src="../images/component/header/logo.png" alt="logo" class="header__login__img" >
      <div class="header__login__bottom_2"></div>
    </div>
    <div class="header__nav">
      <div class="header__NavBox " :class="{'header__nav--BottomLine':!OriclOne}">
        <div class="header__oricl" >
          <img src="../images/component/header/nav_line.png" alt="" v-show="OriclOne">
        </div>
        <a href="#">首页</a>
      </div>
      <div class="header__NavBox " :class="{'header__nav--BottomLine':!OriclTwo}">
        <div class="header__oricl">
          <img src="../images/component/header/nav_line.png" alt="" v-show="OriclTwo">
        </div>
        <a href="#" style="color:rgba(177, 218, 217, 0.5)">自我评估</a>
      </div>
      <div class="header__NavBox" :class="{'header__nav--BottomLine':!OriclThree}">
        <div class="header__oricl">
          <img src="../images/component/header/nav_line.png" alt="" v-show="OriclThree">
        </div>
        <a href="#" style="color:rgba(177, 218, 217, 0.5)">名校展示</a>
      </div>
    </div>
    <div class="header__Right">
      <div class="header__login"><a href="#">登入</a><span>&nbsp&nbsp\&nbsp&nbsp</span><a href="#">注册</a></div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'header',
  computed: {
    OriclOne () {
      return this.$route.path === '/'
    },
    OriclTwo () {
      return this.$route.path === '/MyTarget'
    },
    OriclThree () {
      return this.$route.path === '/school'
    }
  }

}
</script>

<style scoped>
 a{
  display: block;
  color:white;
  text-align: center;
  line-height: 80px;
  text-decoration:none;
  font-size: 13px;
  color:rgb(177, 218, 217)
}
.header__nav a{
  cursor: default;
}
.header{
  height: 60px;
  width:100%;


}
.header__logo{
  width: 30%;
  height:100%;
  float: left;
  position: relative;

}
.header__login__img{
  position:absolute;
  width:30%;

}
.header__login__bottom_1{
  border-bottom:1px solid rgb(177, 218, 217);
  height:100%;
  width:20%;
  position: absolute;

}
.header__login__bottom_2{
  border-bottom:1px solid rgb(177, 218, 217);
  height:100%;
  width:45%;
  position: absolute;
  right: 0;
  overflow: hidden;
}
.header__logo img{
  left:20%;
  bottom:-20%;
}
.header__nav{
  width:40%;
  height: 60px;
  float: left;


}
.header__nav--BottomLine{
  border-bottom:1px solid rgb(177, 218, 217);

}
.header__NavBox{
  position: relative;
  width:33.33%;
  height:100%;
  float: left;

}
.header__oricl{
  position:absolute;
  z-index:99;
  bottom: -1px;
  width:100%;
  height: 45px;
}
.header__oricl img{
  width: inherit;
  height: inherit;
}
.header__Right{
  width: 30%;
  height:100%;
  float: left;
  position: relative;
  border-bottom:1px solid rgb(177, 218, 217);
}
.header__login{
  position: absolute;
  right: 20%;
}
.header__login a{
  display: inline;
  color: rgba(177, 218, 217, 0.5);

}
.header__login a:hover{
  color:rgb(177, 218, 217);
}
.header__login span{
  color: rgba(177, 218, 217, 0.5);
  font-size: 1.5em;
  font-weight: lighter;
}
</style>



