import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    { path: '/MyTarget', name: 'MyTarget', component: resolve => require(['../view/MyTarget.vue'], resolve) },
    { path: '/', name: 'home', component: resolve => require(['../view/Home.vue'], resolve) }
  ]
})



